@charset "UTF-8";
//리셋 관련 모음
body, p,
h1, h2, h3, h4, h5, h6,
ul, ol, li,
dl, dt, dd,
table, th, td,
form, fieldset, legend, input, textarea, button, select {
	margin: 0;
	padding: 0;
    -webkit-text-size-adjust:none;
}

body,
input,
textarea,
select,
button,
table {
    font-family: -apple-system,BlinkMacSystemFont,helvetica,Apple SD Gothic Neo,sans-serif;
    font-size: 14px;
    -webkit-font-smoothing: subpixel-antialiased;
	line-height:20px;
	color: #333;
}

img,
fieldset {
	border: 0;
    vertical-align: top;
}

ul, ol {
	list-style: none;
}

em, address {
	font-style: normal;
}

a,a:hover, a:active, a:focus {
	text-decoration: none;
}

.blind {
	overflow: hidden;
	position: absolute;
	top: 0;
	left: 0;
	margin: -1px;
	padding: 0;
	width: 1px;
	height: 1px;
	border: 0;
	clip: rect(0, 0, 0, 0);
}
